'use client'
import { useAuth } from "@/components/AuthContext" 
import { useEffect } from "react"
import { useRouter } from "next/navigation"
import {
  Table,
  TableBody,
  TableCaption,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"


export default function Page(){
    const {isAuthenticated} = useAuth()
    const router = useRouter()
  

    useEffect(()=>{
       if(!isAuthenticated){
            router.push('/login')
        }
    },[isAuthenticated])
    return(
        <div className="w-full">
            <div className="flex justify-end px-12 py-4">
              <input className="max-w-md bg-white px-4 py-3 border border-gray-300 rounded-lg shadow-sm focus:outline-none focus:ring-2 focus:ring-blue-500" 
              placeholder="search"
              />
            </div>
            <div>

            </div>
            
        </div>
    )
}